.banner {
    z-index: 100;
    position: relative;
    .banner-row {
        overflow: hidden;
        display: flex;
        align-items: center;
        @media (max-width: $layout-breakpoint-medium) {
            &:first-child {
                margin-top: 48px;
            }
        }
        &.center {
            justify-content: center;
            text-align: center;
            .row-letter {
                left: 0;
            }
        }
        &.right {
            justify-content: flex-end;
        }
        .row-title,
        .row-letter {
            //  font-size: 24rem;
            font-weight: 500;
            position: relative;
            letter-spacing: -0.8rem;
            display: inline-block;
            white-space: nowrap;
            //@include basekick(24, 24, 0.11, 24, 10, 1);
            //@media (max-width: $layout-breakpoint-large) {
            font-size: 24rem;
            //line-height: 20rem;
            //}
            @media (max-width: $layout-breakpoint-medium) {
                font-size: 18rem;
                line-height: 18rem;
            }
            @media (max-width: $layout-breakpoint-small) {
                font-size: 14rem;
                line-height: 14rem;
            }
            @media (max-width: $layout-breakpoint-xsmall) {
                font-size: 10rem;
                line-height: 10rem;
            }
        }
        .row-col {
            width: 50%;
            display: flex;
            //align-content: center;
            //justify-content: center;
            @media (max-width: $layout-breakpoint-medium) {
                width: 70%;
            }
            @media (max-width: $layout-breakpoint-small) {
            }
            @media (max-width: $layout-breakpoint-xsmall) {
                &:last-child {
                    display: none;
                }
                width: 100%;
                justify-content: flex-start;
            }
            .row-message {
                font-weight: 500;
                font-size: 1.8rem;
                line-height: 2.6rem;
                padding: 0 1rem;
                //width: 320px;
            }
        }
        .scroll {
            height: 160px;
            width: 160px;
            border-radius: 100%;
            background: $white;
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            left: 160px;
            @media (max-width: $layout-breakpoint-medium) {
                left: 60px;
            }
            @media (max-width: 1240px) {
                display: none;
            }
            span {
                font-size: 18px;
                font-weight: 600;
                margin: 4px 0;
                @media (max-width: $layout-breakpoint-xsmall) {
                    font-size: 10px;
                    margin: 2px 0;
                }
            }
        }
    }
}

.transition-image {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
        width: 800px;
        display: flex;
    }
    &.final {
        display: block;
        top: -128px;
        position: relative;
        width: 90%;
        margin: 0 auto;
        z-index: -100;
        top: 0;
        //@media (max-width: $layout-breakpoint-large) {
        //}
        //@media (max-width: $layout-breakpoint-xsmall) {
        //    //top: -1px;
        //}
        img {
            width: 100%;
            max-width: 100%;
        }
    }
}
